#icons{
    display :flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    gap: 10px;
}

@media all and (max-width:500px) and (min-width :200px){
    #icons{
    display :flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3px
    }
}